.equipment-container {
    display: flex;
    flex-direction: column;
    border: 1px solid #aaa;
    border-radius: 8px;
    background-color: #f5f5f5;
    padding: 5%;
    width: 100%;
    aspect-ratio: 1 / 1.75;
    box-sizing: border-box; /* 패딩 포함 높이계산 */
  }

.slot-label {
    font-weight: bold;
    margin-bottom: 8px;
    color: #222;
}

.dropdown-grid {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-between;
}

.dropdown-row {
    display: flex;
    gap: 5%;
    width: 100%;
    justify-content: center;
}

.dropdown-divider {
    height: 1px;
    width: 95%;
    background-color: #ccc;
    margin-left: 2.5%;
}

.slot-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

.slot-label {
    font-weight: bold;
    color: #222;
}